import model from '../../components/model/model.vue'
import {
	ajax
} from '../../common/js/util.js'
import {
	Toast
} from 'vant'
export default {
	data() {
		return {
			Color: '#DE2910',
			value: '',
			backColor: '#FFFFFF',
			show: false,
			tel: '',
			user: '',
			card: ''
		}
	},
	components: {
		model,
		Toast
	},
	onLoad() {
		//刷新保留用户uid
		if (this.getRequest('uid')) {
			var uids = this.getRequest('uid')
			this.$store.commit('changeUid', uids)
		}
	},
	onReady() {

	},
	methods: {
		getRequest(variable) {
			var query = window.location.search.substring(1);
			var vars = query.split("&");
			for (var i = 0; i < vars.length; i++) {
				var pair = vars[i].split("=");
				if (pair[0] == variable) {
					return pair[1];
				}
			}
			return (false);
		},
		bindCard() {
			if (this.user == '') {
				Toast('姓名不能为空')
				return
			}

			if (this.tel == '') {
				Toast('手机号不能为空')
				return
			} else if (!(/^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/.test(this.tel))) {
				Toast('手机号格式不正确')
				return
			}

			if (this.card == '') {
				Toast('会员卡号不能为空')
				return
			}

			let self = this
			let datas = {
				url: '/api/gzh/bindCard',
				data: {
					uid: this.$store.state.uid,
					cardId: this.card,
					username: this.user,
					phone: this.tel
				},
				success: function(res) {
					console.log(res)
					if (res.data.result == 0) {
						self.show = true
					} else {
						Toast(res.data.resultNote)
					}
				},
				fail: function(res) {
					uni.showToast({
						title: res.data.resultNote,
						icon: 'none'
					});
				}
			}
			ajax(datas)
		},
		confirm() {
			this.show = false
		}
	}
}
